.container{
    margin: 20px 0px;    
    
}

.card{
    margin : 0 2%;
   
    
}

.infected{
    border-bottom: 10px solid rgba(255,255,0,0.5);
}
.recovered{
    border-bottom: 10px solid rgba(0,255,0,0.5);
    margin-left: 10px;
}
.deaths{
    border-bottom: 10px solid rgba(255,0,0,0.5);
}

@media(max-width:770px){
    .card{
        margin : 2% 0 !important;
    }

}